import { Component, OnInit } from '@angular/core';
import {toRoute} from 'src/route/route';

@Component({
  selector: 'app-post-bar',
  templateUrl: './post-bar.component.html',
  styleUrls: ['./post-bar.component.css']
})
export class PostBarComponent implements OnInit {
  public routes: any;

  constructor() {
    this.routes = toRoute.filter((el) => {
      return el.city !== 'Vyazma' &&
              el.city !== 'Smolensk' &&
              el.city !== 'Minsk' &&
              el.city !== 'Warsawa' &&
              el.city !== 'Lodz';
    });
  }

  ngOnInit() {
  }

}
