import { Component, OnInit } from '@angular/core';
import {toRoute} from 'src/route/route';
import * as L from 'leaflet';

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css']
})
export class MapComponent implements OnInit {
  public options: any;
  public layersControl: any;
  public layers: any = [];
  public DefaultIcon: any;
  private route: any = [];
  private token = 'pk.eyJ1IjoiYXJrYWRpcm8iLCJhIjoiY2p2OGc3OHViMDR6bDRkcDcwampsaWF5OCJ9.GOYN004zUExc7DuUQgIQwg';

  constructor() { }

  ngOnInit() {
    this.uploadIcons();
    this.initMap();
    this.drawLayers();
    this.drawLine(toRoute);
  }

  private drawLine(route) {
    this.route = route.map(city => city.point);
    const backroute = this.route.slice(0, route.length - 1).reverse();
    this.route = this.route.concat(backroute);
    route = route.filter((el) => {
      return el.city !== 'Vyazma' &&
              el.city !== 'Smolensk' &&
              el.city !== 'Minsk' &&
              el.city !== 'Warsawa' &&
              el.city !== 'Lodz';
    });
    route.forEach(el => {
      this.layers.push(L.marker(el.point).bindPopup(`<b>${el.city}</b>`).openPopup());
    });
    this.layers.push(L.polygon(this.route));
  }

  private uploadIcons() {
    delete L.Icon.Default.prototype._getIconUrl;
    L.Icon.Default.mergeOptions({
      iconRetinaUrl: './assets/marker-icon-2x.png',
      iconUrl: './assets/marker-icon.png',
      shadowUrl: './assets/marker-shadow.png',
    });
  }

  private initMap() {
    this.options = {
      layers: [
        L.tileLayer(`https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token=${this.token}`, {
          maxZoom: 18,
          id: 'mapbox.streets',
          accessToken: this.token,
          attribution: '...'
        })
      ],
      zoom: 4,
      scrollWheelZoom: false,
      // Warsawa
      center: L.latLng(52.23, 20.92)
    };
  }

  private drawLayers() {
    this.layersControl = {
      baseLayers: {
        'Default': L.tileLayer(`https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token=${this.token}`, {
          maxZoom: 18,
          id: 'mapbox.streets',
          accessToken: this.token,
          attribution: '...'
        }),
        'Open Street Map': L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', { maxZoom: 18, attribution: '...' }),
        // 'Open Cycle Map': L.tileLayer('http://{s}.tile.opencyclemap.org/cycle/{z}/{x}/{y}.png', { maxZoom: 18, attribution: '...' })
      },
      overlays: {
        // 'Big Circle': L.circle([ 46.95, -122 ], { radius: 5000 }),
        // 'Line': L.polygon(route)
      }
    };
  }

}
